
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
  background: #000;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
  color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcart .content .content-left .logo-wrapper {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 50%;
  background: #141d27;
}
.shopcart .content .content-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2d343c;
  text-align: center;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  font-size: 24px;
  color: #80858a;
  line-height: 44px;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  padding-right: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 16px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left .price.highlight {
  color: #fff;
}
.shopcart .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  margin: 12px 0 0 12px;
  line-height: 24px;
  font-size: 10px;
}
.shopcart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
}
.shopcart .content .content-right .pay {
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  background: #2b333b;
}
.shopcart .content .content-right .pay.not-enough {
  background: #2b333b;
}
.shopcart .content .content-right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shopcart .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  /*&.drop-enter-to,&.drop-leave{
				transition: all 0.4s;
				.inner{
					transition: all 0.4s;
				}
			}*/
}
.shopcart .ball-container .ball.drop-enter-active,
.shopcart .ball-container .ball.drop-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(0.49, -0.29, 0.75, 0.41);
  transition: all 0.5s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .ball-container .ball.drop-enter-active .inner,
.shopcart .ball-container .ball.drop-leave-active .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.shopcart .shopcart-list {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter-active,
.shopcart .shopcart-list.fold-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.shopcart .shopcart-list.fold-enter,
.shopcart .shopcart-list.fold-leave-to {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background: #fff;
}
.shopcart .shopcart-list .list-content ul .food {
  padding: 12px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
.shopcart .shopcart-list .list-content ul .food:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: "";
}
.shopcart .shopcart-list .list-content ul .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shopcart .shopcart-list .list-content ul .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcart .shopcart-list .list-content ul .food .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(7, 17, 27, 0.6);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.list-mask.fade-enter-to,
.list-mask.fade-leave {
  opacity: 1;
}
.list-mask.fade-enter,
.list-mask.fade-leave-to {
  opacity: 0;
}



