
.ratings {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.ratings .overview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
}
.ratings .overview .overview-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 137px;
          flex: 0 0 137px;
  padding: 6px 0;
  width: 137px;
  border-right: 1px solid rgba(7, 17, 27, 0.1);
  text-align: center;
}
@media only screen and (max-width: 320px) {
.ratings .overview .overview-left {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 120px;
            flex: 0 0 120px;
    width: 12pxpx;
}
}
.ratings .overview .overview-left .score {
